  content: '';
  position: absolute;
  height: 18px;
  width: 18px;
  color: #FFFFFF;
  border-radius: 4px;
  background-image: svg-load('checkbox.svg');
  opacity: 0;
  transition: 0.2s;
}
.checkbox-icon::after {
  position: absolute;
  content: '';
  height: 20px;
  width: 20px;
  border-radius: 4px;
  border: 2px solid #CCCCCC;
  transition: 0.2s;
}
