@font-face {
	font-family: 'icons';
	src: url('../fnt/icons.eot');
	src: url('../fnt/icons.eot?#iefix') format('eot'),
		url('../fnt/icons.woff2') format('woff2'),
		url('../fnt/icons.woff') format('woff'),
		url('../fnt/icons.ttf') format('truetype'),
		url('../fnt/icons.svg#icons') format('svg');
}
@font-face {
	font-family: 'Euclid Square';
	src: url('../fnt/EuclidSquare-Bold.eot');
	src: local('Euclid Square Bold'), local('EuclidSquare-Bold'),
		url('../fnt/EuclidSquare-Bold.eot?#iefix') format('embedded-opentype'),
		url('../fnt/EuclidSquare-Bold.woff2') format('woff2'),
		url('../fnt/EuclidSquare-Bold.woff') format('woff'),
		url('../fnt/EuclidSquare-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

